#app {
  margin: 0 auto;
  max-width: 375 * @rem;
}

.lichang a {
  font-size: medium;
  color: #ffffff;
}

.mail a {
  color: #ffffff;
  font-weight: bold;
}

.right {
  text-align: right;
}

.box {
  border: 1px solid #ebebeb;
  border-radius: 5px;
  padding: 20px;
  margin: 10px;
}

.text {
  // border: 1px solid #ebebeb;
  padding: 10px;
  margin-bottom: 30px;
}

/* .el-select .el-input {
  width: 130px;
} */
.input-with-select {
  width: 80px;
  // color: #ffffff;
  // background-color: #f44336;
}

.el-container {
  height: 100%;
}

.el-header {
  background-color: #f44336;
  color: #ffffff;
  text-align: center;
  line-height: 60px;
}

.el-footer {
  background-color: #f44336;
  color: #ffffff;
  text-align: center;
  line-height: 40px;
}

/* .el-tabs {
  text-align: center;
} */

/* .el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 200px;
} */

.el-main {
  background-color: #ffffff;
  color: #333;
  text-align: center;
  // line-height: 160px;
}

/* body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
} */

.lang_box {
  line-height: 70px;
  width: 48px;
  height: 50px;
  box-sizing: border-box;
  position: relative;
}
.lang_box .lang_header {
  position: absolute;
  top: 15px;
  left: 0;
  width: 48px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff73;
  border-radius: 10px;
}
.lang_box .lang_content {
  position: absolute;
  top: 45px;
  left: 0;
  width: 48px;
  background: #494949b4;
  border-radius: 5px;
}
.lang_box .lang_content .lang_item {
  width: 48px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.lang_box .lang_content .lang_item:hover {
  background: #ffffff73;
  border-radius: 5px;
}

.flexx {
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden; /*这个参数根据需要来绝对要不要*/
}
</style>



